"use client";

import Editor from "@monaco-editor/react";
import { useState } from "react";
import * as actions from '../actions';

export default function IDE() {
  const [code, setCode] = useState('print("hello world")');
  const [output, setOutput] = useState<string[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState("");

  const handleChange = (value: string = "") => {
    setCode(value);
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    await execute(code);
  };

  const execute = async (code: string) => {
    setIsLoading(true);
    setError("");
    setOutput(prev => [...prev, "$ Executing code..."]);

    try {
      const result = await actions.executeCode(code);
      
      setOutput(prev => [
        ...prev,
        ...result.output.split("\n"),
        `\nExecution finished with exit code ${result.exitCode}`
      ]);
      
      if (result.error) {
        setError(result.error);
      }
    } catch (err) {
      setError(`Error: ${err instanceof Error ? err.message : String(err)}`);
      setOutput(prev => [...prev, "Execution failed"]);
    } finally {
      setIsLoading(false);
    }
  };

  const clearOutput = () => {
    setOutput([]);
    setError("");
  };


    return (
      <div>
        <Editor
            height="50vh"
            defaultLanguage="python"
            theme="vs-dark"
            defaultValue='print("hello world")'
            options={{minimap: {enabled: false}}}
            onChange={handleChange}
        />
        <form onSubmit={handleSubmit}>
          <button type = "submit">Submit</button>
        </form>

        <div className="bg-white w-[full h-[200px]">
            <span className="text-center text-black"> {output} </span>
        </div>
      </div>
    );
  }